// import React from "react";
import React, {  useEffect, useState } from "react";
import Navbar from "../navbar/Navbar";
import Axios from "axios";
import { api } from "../../api";
import { useHistory } from "react-router";
import Swal from "sweetalert2";

function ChangePass() {
  const history = useHistory();
  const [Pass, setPass] = useState("");

  const [oldPass, setOldPass] = useState("");
  const [newPass, setNewPass] = useState("");
  const [confirmNewPass, setConfirmNewPass] = useState("");

  useEffect(async () => {
    await Axios.get(api + "password").then((res) => {
      // const { data } = res;
      setPass(res.data.data.config_value);
    });
  }, []);

  const updatePass = async () => {
    await Axios.put(api + "password", { newpassword: newPass });
  };

  // console.log("lengh = " + newPass.length);

  const savePass = () => {
    if (newPass != confirmNewPass) {
      Swal.fire({
        icon: "error",
        title: "เปลี่ยนรหัสผ่านผิดพลาด",
        text: "กรุณากรอกรหัสผ่านใหม่ให้ตรงกัน",
      });
    } else if (newPass.length < 4 || newPass.length > 12) {
      Swal.fire({
        icon: "error",
        title: "เปลี่ยนรหัสผ่านผิดพลาด",
        text: "กรุณากรอกรหัสผ่านโดยมีความยาว 4 - 12 ตัวอักษร",
      });
    } else if (Pass != oldPass) {
      Swal.fire({
        icon: "error",
        title: "เปลี่ยนรหัสผ่านผิดพลาด",
        text: "กรุณากรอกรหัสผ่านเดิมให้ถูกต้อง",
      });
    } else {
      Swal.fire({
        title: "ยืนยันเปลี่ยนรหัสผ่าน",
        text: "คุณต้องการที่จะบันทึกรหัสผ่านใหม่ใช่หรือไม่",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        cancelButtonText: "ยกเลิก",
        confirmButtonText: "บันทึก",
      }).then((result) => {
        if (result.isConfirmed) {
          history.push("wait")
          updatePass();
          Swal.fire({
            position: "center",
            icon: "success",
            title: "บันทึกรหัสผ่านสำเร็จ",
            showConfirmButton: false,
            timer: 1500,
          }).then(() => {
            setOldPass("");
            setNewPass("");
            setConfirmNewPass("");
            history.push("changePass");
          });
        }
      });
    }
  };
  // console.log("pass = " + Pass);
  // console.log("passOld = " + oldPass);
  // console.log("passNew = " + newPass);
  // console.log("passconfirm = " + confirmNewPass);

  return (
    <div>
      <Navbar title="pass" />
      <div className="row">
        {/* <div className="col-md-3 "></div> */}
        <div className="col-md-6 changePass">
          <h2>เปลี่ยนรหัสผ่าน</h2>
          <div className="box-cheange">
            <p>รหัสผ่านเดิม</p>
            <input
              className="form-control"
              type="password"
              placeholder="กรุณากรอกรหัสผ่านเดิม"
              value={oldPass}
              onChange={(e) => {
                setOldPass(e.target.value);
              }}
            />
            <br />
            <p>รหัสผ่านใหม่</p>
            <input
              className="form-control"
              type="password"
              value={newPass}
              placeholder="กรุณากรอกรหัสผ่านโดยมีความยาว 4 - 12 ตัวอักษร"
              onChange={(e) => {
                setNewPass(e.target.value);
              }}
            />
            <br />

            <p>ยืนยันรหัสผ่านใหม่</p>
            <input
              className="form-control"
              type="password"
              placeholder="กรุณายืนยันรหัสผ่านใหม่"
              value={confirmNewPass}
              onChange={(e) => {
                setConfirmNewPass(e.target.value);
              }}
            />
            <br />
            <div className="btn-change">
              <div>
                <button
                  type="button"
                  className="btn btn-outline-primary"
                  onClick={() => savePass()}
                >
                  บันทึก
                </button>
                <button type="button" className="btn btn-outline-danger">
                  ยกเลิก
                </button>
              </div>
            </div>
          </div>
        </div>
        {/* <div className="col-md-3 "></div> */}
      </div>
    </div>
  );
}

export default ChangePass;
