// import { contextStatuslock } from "../dashboard/Dashbord";
import Navbar from "../navbar/Navbar";
import React, { useState, useEffect, createContext } from "react";
import { api, api4 } from "../../api";
import { lastState } from "../navbar/NavUnlock";

import Axios from "axios";
import Sub from "./Sub";

const boxDataLog = createContext();

function TableData() {
  const statusLock = lastState;
  // const [countD, setcountD] = useState("");
  const [dataLog, setDataLog] = useState([]);
  const [dataDevice3, setDataDevice3] = useState([]);
  const [selectTel, setSelectTel] = useState("");

  useEffect(async () => {
    fechDatalog();
  }, []);

  const fechDatalog = async () => {
    await Axios.get(api4 + "datalog4-section1").then((res) => {
      // const { data } = res;
      setDataLog(res.data.data);
      // setcountD(res.data.data.device[0].length);
    });
  };

  console.log(dataLog);

  return (
    <div>
      {/* <contextStatuslock.Provider> */}
      <Navbar title="table" />

      <div className="row all-title">
        <div className="col-md-3 ">
          <div className="crad-items">
            <p className="title-crad-items">อุปกรณ์ทั้งหมด</p>
            <p className="data-crad-items">{Number(dataLog.device4_count)} อุปกรณ์</p>
          </div>
        </div>
        <div className="col-md-3">
          <div className="crad-items">
            <p className="title-crad-items">ยอดรวม</p>
            <p className="data-crad-items">{Number(dataLog.total).toFixed(2)} บาท</p>
          </div>
        </div>
        <div className="col-md-3 ">
          <div className="crad-items">
            <p className="title-crad-items">ยอดรวม ONLINE</p>
            <p className="data-crad-items">{Number(dataLog.sumonline).toFixed(2)} บาท</p>
          </div>
        </div>
        <div className="col-md-3 ">
          <div className="crad-items">
            <p className="title-crad-items1">ยอดรวมเหรียญ</p>
            <p className="data-crad-items1">{Number(dataLog.sumcoin).toFixed(2)} บาท</p>
            <hr />
            <p className="title-crad-items1">ยอดรวมธนบัตร</p>
            <p className="data-crad-items1">{Number(dataLog.sumbanknote).toFixed(2)} บาท</p>
          </div>
        </div>
      </div>
      <boxDataLog.Provider value={{ fechDatalog }}>
        <Sub statusLock={statusLock} />
      </boxDataLog.Provider>
    </div>
  );
}

export { boxDataLog };
export default TableData;
