import React, { useContext, useEffect, useState } from "react";
import { contextStatuslock } from "../dashboard/Dashbord";
import Swal from "sweetalert2";
import { useHistory } from "react-router";
import NavUnlock from "./NavUnlock";
// import { realPass } from "../Pass";
import Axios from "axios";
import { api } from "../../api";

// var setStatusLockNow;
var stateMenu = false;
function Navbar({ title }) {
  
  const head = title;

  var fontD;
  var fontT;
  var fontP;
  if (head == "dashbord") {
    fontP = 16;
    fontT = 16;
    fontD = 20;
  }
  if (head == "table") {
    fontP = 16;
    fontT = 20;
    fontD = 16;
  }
  if (head == "pass") {
    fontP = 20;
    fontT = 16;
    fontD = 16;
  }
  // ////console.log("fontP = " + fontP);
  // ////console.log("fontT = " + fontT);
  // //////console.log("fontD = " + fontD);

  const history = useHistory();
  const [pass, setPass] = useState([]);
  useEffect(async () => {
    await Axios.get(api + "password").then((res) => {
      // const { data } = res;
      setPass(res.data.data.config_value);
    });
  }, []);

  const pageChangePass = () => {
    setStateMenum(false);

    history.push("changePass");
  };
  const pageTableData = () => {
    history.push("table-data");
  };
  const [stateMenum, setStateMenum] = useState(false);

  const setStateM = () => {
    // setStateM(!stateMenu);
    stateMenu = !stateMenu;
  };
  // console.log("stateMenu = " + stateMenum);
  return (
    <div className="nav navber">
      <ul className="ul-bg">
        <li style={{ fontSize: fontD }} onClick={() => history.push("/")}>
          Dashbord
        </li>
        &emsp;/
        <li style={{ fontSize: fontT }} onClick={() => pageTableData()}>
          Data Log
        </li>
        &emsp;/
        <li style={{ fontSize: fontP }} onClick={() => pageChangePass()}>
          Change Password
        </li>
      </ul>

      <div className="unlock">
        <NavUnlock passWordd={pass} />
      </div>
      <div
        className="iconmenu"
        onClick={() => {
          setStateM();
          setStateMenum(!stateMenum);
        }}
      >
        <i class="bi bi-list "></i>
      </div>

      {stateMenu ? (
        <ul className="navPhone">
          <li
            style={{ fontSize: fontD }}
            onClick={() => {
              history.push("/");
              setStateMenum(false);
            }}
          >
            Dashbord
          </li>
          <li
            style={{ fontSize: fontT }}
            onClick={() => {
              pageTableData();
              setStateMenum(false);
            }}
          >
            Data Log
          </li>
          <li
            style={{ fontSize: fontP }}
            onClick={() => {
              pageChangePass();
            }}
          >
            Change Password
          </li>
        </ul>
      ) : (
        <></>
      )}
    </div>
  );
}

export default Navbar;
