import React, { useContext, useEffect, useState } from "react";
import { contextStatuslock } from "../dashboard/Dashbord";
import Swal from "sweetalert2";
import { useHistory } from "react-router";

var lastState = false;

function NavUnlock({ passWordd }) {
  const history = useHistory();
  var swLock = false;

  var password = passWordd;
  var setStatusLockNow = true;
  
  const handle = () => {
    swLock = true;
    localStorage.setItem("Status", swLock);
  };
  const remove = () => {
    swLock = false;
    localStorage.setItem("Status", swLock);
  };

  const unlock = () => {
    Swal.fire({
      title: "กรุณากรอกรหัสผ่าน",
      text: "กรอกรหัสผ่านสำหรับแก้ไขข้อมูลทั้งหมด",
      icon: "info",
      input: "password",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      cancelButtonText: "ยกเลิก",
      confirmButtonText: "ยืนยัน",
    }).then((result) => {
      if (result.value) {
        const inputPassword = result.value;
        if (inputPassword == password) {
          lastState = true;
          handle();
          Swal.fire({
            position: "center",
            icon: "success",
            title: "รหัสผ่านถูกต้อง",
            showConfirmButton: false,
            timer: 1500,
          }).then(() => {
            history.push("/");
          });
        } else {
          remove();
          Swal.fire({
            position: "center",
            icon: "error",
            title: "กรอกรหัสผ่านผิด",
            showConfirmButton: false,
            timer: 2000,
          });
        }
      }
    });
  };

  const lock = () => {
    Swal.fire({
      title: "กรุณากรอกรหัสผ่าน",
      icon: "info",
      input: "password",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      cancelButtonText: "ยกเลิก",
      confirmButtonText: "ยืนยัน",
    }).then((result) => {
      if (result.value) {
        const inputPassword = result.value;
        if (inputPassword == password) {
          lastState = false;

          remove();
          Swal.fire({
            position: "center",
            icon: "success",
            title: "รหัสผ่านถูกต้อง",
            showConfirmButton: false,
            timer: 1500,
          }).then(() => {
            history.push("/");
          });
        } else {
          Swal.fire({
            position: "center",
            icon: "error",
            title: "กรอกรหัสผ่านผิด",
            showConfirmButton: false,
            timer: 2000,
          });
          handle();
        }
      }
    });
  };

  const setDefault = () => {
    setTimeout(() => {
      remove();
      history.push("/");
    }, 300000);
  };
  // console.log("lastState = " + lastState);

  return (
    <div>
      {!lastState ? (
        <div
          className="logo-lock"
          onClick={() => {
            unlock();
          }}
        >
          <i className="bi bi-lock-fill"></i>
        </div>
      ) : (
        <div
          className="logo-unlock"
          onClick={() => {
            lock();
          }}
        >
          <i className="bi bi-unlock-fill"></i>
        </div>
      )}
    </div>
  );
}
export { lastState };
export default NavUnlock;
