// import { Input } from "@material-ui/core";
import React, { useState, useEffect } from "react";
import Navbar from "../navbar/Navbar";
import { lastState } from "../navbar/NavUnlock";
// import Dashbord from "./Dashbord";
// import DashbordLock from "./DashbordLock";
import DashbordUnlock from "./DashbordUnlock";
import { api } from "../../api";
import Swal from "sweetalert2";
import Axios from "axios";
import { useHistory } from "react-router";

// import {lastState} from ""
function Home() {
  const history = useHistory();

  const statusLock = lastState;
  // const statusLock = true;
  const [WIFIname, setWIFIname] = useState();
  const [WIFIpass, setWIFIpass] = useState();
  const [LINEtoken, setLINEtoken] = useState();
  // const statusLock = true;
  const lock = () => {
    Swal.fire({
      position: "center",
      icon: "info",
      title: "กรุณาปลดล็อค",
      showConfirmButton: false,
      timer: 2000,
    });
  };

  const editConfig = async (input, name) => {
    console.log("input = " + input + ",name = " + name);
    await Axios.put(api + "config", {
      config_name: name,
      config_value: input,
    });
  };

  const updateConfig = (input, name) => {
    Swal.fire({
      title: "แก้ไขข้อมูล",
      text: "ยืนที่จะแกไขข้อมูลของ " + name,
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "ยืนยัน",
      cancelButtonText: "ยกเลิก",
    }).then((result) => {
      if (result.isConfirmed) {
        // fechDeleteDevice(id, tel);
        // clearDeviceAll(tel);
        editConfig(input, name);
        history.push("wait");
        Swal.fire({
          position: "center",
          icon: "success",
          title: "แก้ไขข้อมูลสำเร็จ",
          showConfirmButton: false,
          timer: 1500,
        }).then(() => {
          history.push("/");
        });
      }
    });
  };

  useEffect(() => {
    Axios.get("https://api.pcdservice.info/config").then((res) => {
      console.log(res.data.data[1].config_value);
      setLINEtoken(res.data.data[1].config_value);
      setWIFIname(res.data.data[2].config_value);
      setWIFIpass(res.data.data[3].config_value);
    });
  }, []);

  const saveNameWIFI = (input, name) => {
    if (statusLock == false) {
      lock();
    }
    if (statusLock == true) {
      //////console.log(" WIFIname = " + WIFIname);
      updateConfig(input, name);
    }
  };

  const savePassWIFI = (input, name) => {
    if (statusLock == false) {
      lock();
    }
    if (statusLock == true) {
      updateConfig(input, name);
      ////console.log(" WIFIpass = " + WIFIpass);
    }
  };

  const saveLineToken = (input, name) => {
    if (statusLock == false) {
      lock();
    }
    if (statusLock == true) {
      updateConfig(input, name);
      ////console.log(" LINEtoken = " + LINEtoken);
    }
  };

  ////console.log("lastState === " + lastState);
  return (
    <div>
      <Navbar title="dashbord" />
       <DashbordUnlock
          stateL={statusLock}
          wifiName={WIFIname}
          wifiPass={WIFIpass}
          lineToken={LINEtoken}
        />
      
    </div>
  );
}

export default Home;
